// search box

.home-search-form-container {
  padding: 0;
  margin-top: -100px;
}

.main-search-box {
  position: relative;
  z-index: 100;
  background: rgba(255, 255, 255, 0.85);
  padding: 16px 32px;
  border-radius: 8px;
  box-shadow: 0px 5px 25px 0px rgba(0, 0, 0, 0.16);

  h1 {
    color: $color-blue;
    margin: 0 0 0.5rem 0;
  }

  p {
    font-size: 15px;
  }

  @media only screen and (max-width: 450px) {
    .d-flex {
      flex-direction: column;

      .btn {
        margin: 0 auto;
        width: 100%;

        &:nth-child(1) {
          margin-bottom: 24px !important;
        }
      }
    }
  }
  @media screen and (min-width: 450px) and (max-width: 768px) {
    .btn {
      width: max-content;
      min-width: 10rem;
      font-size: .9rem;
      margin-bottom: 4px !important;
    }
  }
  @media screen and (min-width: 768px) {
    .btn {
      width: 40%;
      max-width: 15rem;
      font-size: .9rem;
      margin-bottom: 4px !important;
    }
  }
  @media screen and (min-width: 992px) {
    .btn {
      width: max-content;
      font-size: .9rem;
      margin-bottom: 4px !important;
    }
  }
  @media screen and (min-width: 1200px) {
    .btn {
      font-size: 1rem;
      margin-bottom: 4px !important;

      &:nth-child(1) {
        width: 8rem;
      }
    }
  }
}

.rental-solution-block {
  h2 {
    margin: 24px auto 26px;
    color: $color-blue;
  }
}

.medical-travel-block {
  background: $color-lightblue;

  h2 {
    margin: 18px auto 94px;
    color: #fff;
  }

  p {
    color: #fff;
  }

  img {
    display: block;
    border-radius: 8px;
    height: auto;
  }

  .texts-block {
    padding-left: 128px;

    .user-btn {
      color: #fff;
      border: 2px solid $color-yellow;
      border-radius: 10px;
      transition: all ease-in-out 300ms;

      &:hover {
        color: $color-blue;
        background-color: #fff;
      }
    }

    @media (max-width: 992px) {
      padding-left: 0;
    }
  }
}

.insurance-block {
  padding: 96px 0;
  background: #F8F8F8;

  .texts-block {
    padding-left: 128px;

    h2 {
      margin: 0 auto 33px;
    }
  }

  @media (max-width: 992px) {
    padding: 50px 0;
    .texts-block {
      padding-left: 0;
    }
  }
}

.corporate-housing-block {
  padding: 96px 0;

  h2 {
    margin: 0 auto 33px;
    color: $color-blue;
  }


  img {
    display: block;
    border-radius: 8px;
    box-shadow: 0 10px 20px #ccc;
    max-width: 100%;
    height: auto;
  }

  .btn {
    min-width: 15rem;
  }

  @media (max-width: 992px) {
    padding: 50px 0;
  }
}

.stars-experience-block {
  padding: 145px 24px;

  h2 {
    margin: 0 auto 76px;
  }

  &.stars-background {
    background: linear-gradient(to right, rgba(255, 255, 255, 0.85), rgba(255, 255, 255, 0.85)),
    url(../../assets/room3.jpg) no-repeat center;
    background-size: cover;
    text-align: center;

    h4 {
      color: $color-darkgray !important;
    }

    .stars-icon {
      max-width: 120px;
    }

    .stars-item {
      margin-bottom: 3rem;

      img {
        margin: 0 0 1.5rem;
      }

      h4 {
        font-family: Raleway, sans-serif;
        font-size: 1.2rem;
        font-weight: bold;
        text-transform: uppercase;
        color: #3a3a3a;
        margin: 0 0 1rem 0;
      }

      p {
        font-size: 1.125rem !important;
      }
    }

    .vip-services {
      text-align: left;

      h3 {
        margin: 0 0 3rem;
        font-weight: bold;
        font-size: 1.875rem;
      }
    }
  }

}

.stay-boston-block {
  padding: 77px 24px;
  background-color: #F8F8F8;

  h2 {
    margin: 0 auto 25px;
    color: $color-blue;
  }

  ul {
    li {
      font-size: 1rem;
      line-height: 2.2rem;
      padding-bottom: 1rem;

      i {
        margin-right: 0.5rem;
      }
    }
  }

  img {
    display: block;
    border-radius: 8px;
    box-shadow: 0 10px 20px #ccc;
    max-width: 100%;
    height: auto;
  }

  .btn {
    min-width: 15rem;
  }
}

.explore-locations-block {
  padding: 120px 0;

  h2 {
    margin: 0 auto 45px;
    color: $color-blue;
    text-align: center;
  }

  .location-card {
    position: relative;
    max-width: 350px;
    box-shadow: 0px 5px 25px 0px rgba(0, 0, 0, 0.16);
    border-radius: 10px;
    transition: all ease 0.3s;

    .bg-box {
      width: 100%;
      padding-top: 100%;
      background-position: center;
      background-size: cover;
      border-radius: 10px;
      opacity: 1;
      transition: opacity ease-in-out 300ms;
    }

    &:hover {
      .bg-box {
        opacity: .7;
      }
    }

    a {
      position: absolute;
      top: 18px;
      left: 18px;
      right: 18px;
      padding: 18px 20px;
      background-color: white;
      border-radius: 10px;
      font-family: PT Serif, sans-serif;
      font-size: 24px;
      line-height: 1;
      text-align: center;
      color: $color-blue;
    }
  }

  .btn {
    min-width: 15rem;
  }

  @media screen and (max-width: 992px) {
    padding: 50px 0;
  }
}

.look-rentals-block {
  padding: 167px 0;
  background-color: #f8f8f8;

  h2 {
    margin: 10px auto 30px;
    color: $color-blue;
  }

  @media only screen and (max-width: 768px) {
    .d-flex {
      flex-direction: column;
    }
    a {
      padding: 0;

      .btn {
        margin: 1rem auto;
      }
    }
  }

  .btn {
    min-width: 15rem;
  }

  @media screen and (max-width: 992px) {
    padding: 50px 0;
  }
}

.reviews-block {
  padding: 140px 24px;

  h2 {
    margin: 0 auto 16px;
    text-align: center;
    color: $color-blue;
  }


  .carousel-container {
    margin: 0 auto;
    padding: 0 50px;
    width: 100%;

    .slick-slider {
      .slick-list {
        padding: 50px 0;

        .slick-track {
          display: flex;
          align-items: stretch;

          .slick-slide {
            display: flex;
            height: auto;
            margin: 0 12px;
            justify-content: center;

            & > div {
              display: flex;
              justify-content: center;
              align-items: center;
              height: 100%;

              .review-card {
                display: flex;
                flex-direction: column;
                margin: 0 auto;
                padding: 0 0 24px;
                max-width: 370px;
                //min-width: 250px;
                min-height: 450px;
                height: 100%;
                border-radius: 8px;
                background-color: #e8c36a;
                box-shadow: 0px 5px 25px 0px rgba(0, 0, 0, 0.16);

                p {
                  padding: 2rem;
                  line-height: 2rem;
                }

                .reviewer-box {
                  flex-grow: 1;
                  display: flex;
                  flex-direction: column;
                  justify-content: flex-end;
                  align-items: center;

                  h4 {
                    text-align: center;
                  }

                  i {
                    font-size: 1.25rem;
                  }
                }
              }
            }
          }
        }
      }

      .slick-arrow {
        width: 50px;
        height: 50px;
        fill: $color-blue;

        path {
          fill: $color-blue;
        }

        &.slick-prev {
          left: -50px;
        }

        &.slick-next {
          right: -50px;
        }
      }
    }
  }

  @media screen and (max-width: 992px) {
    padding: 50px 0;
    .carousel-container {
      padding: 0 25px;

      .slick-slider {
        .slick-list {
          .slick-slide {
            .review-card {
              p {
                font-size: 1rem;
              }
            }
          }
        }

        .slick-arrow {
          width: 25px;
          height: 25px;

          &.slick-prev {
            left: -25px;
          }

          &.slick-next {
            right: -25px;
          }
        }
      }
    }
  }
}

.subscription-block {
  padding: 130px 24px 120px;
  background: linear-gradient(to right, rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0.8)),
  url(../../assets/subscribe-bg.jpg) no-repeat center;
  background-size: cover;

  .btn {
    min-width: 15rem;
  }
}



