.contact-form {
    margin: 3rem auto;
    width: 100%;

    .input-group {
        padding: 0;
    }

    input:focus,
    textarea:focus {
        border: 3px solid $color-yellow !important;
        box-shadow: none !important;
    }

    input:active,
    textarea:active {
        border: 3px solid $color-yellow !important;
    }

    .btn {
        min-width: 15rem;
    }
}
