/**
** Locations Page
 */
.locations-block {
    padding: 50px 0;

    .btn {
        max-width: 12rem;
    }
}

.image-box {
    img {
        width: 100%;
        object-fit: cover;
        height: auto;
        border-radius: 8px 8px 8px 8px;
        box-shadow: 0px 10px 1px #ddd, 0 10px 20px #ccc;
    }
}
