
.booking-information {
    background: rgba(255, 255, 255, 0.85);
    border-radius: 8px;
    box-shadow: 0px 5px 25px 0px rgba(0, 0, 0, 0.16);
    max-width: 500px;
    margin: 0 auto;
    padding: 2rem;

    h3 {
        margin: 0 auto 24px;
        color: $color-blue;
    }

    input {
        font-size: 0.9rem;
        height: 3rem;
        color: #9a9a9a;
    }

    li {
        font-size: 1rem;
        padding-bottom: 0.5rem;
    }

    hr {
        width: 100%;
    }

    .booking-form-header {
        margin: 0 0 24px;
        display: flex;
        justify-content: space-between;

        h4 {
            margin: 0 auto 24px;
            font-size: 1.75rem;
            padding: 0;

            span {
                font-size: 1.5rem;
                color: #E8C36A !important;
                margin: 0;
                padding: 0;
            }
        }

        @media screen and (max-width: 500px) {
            flex-direction: column;
            text-align: center;
        }
    }

    .booking-information-buttons-wrapper {
        position: absolute;
        bottom: 10px;
        display: flex;
        justify-content: center;

        .btn {
            padding: 8px 10px;
            display: flex;
            justify-content: center;
            align-items: center;
            height: 3rem;

            &:nth-child(1) {
                min-width: 4rem;
                margin-right: 16px;
            }

            &:nth-child(2) {
                font-size: .8rem;
            }
        }
    }

    .form-control:disabled {
        background-color: #fff !important;
    }

    .book-btn {
        min-width: 12rem;
    }
}

.billing-information-container {
    margin: 0 auto 32px;
    input,
    select {
        font-size: 0.9rem;
        height: 3rem;
        margin-bottom: 1rem;
    }
}
