/**
**  Privacy policy
 */
ul.contact-options {
  list-style: none;

  li {
    font-size: 1rem !important;
  }

  li:before {
    content: "-";
    padding-right: 5px;
  }

  span {
    color: $color-lightblue;
    cursor: pointer;
  }
}

/** Coming Soon**/
.styles_typicalWrapper__1_Uvh {
  font-size: 4rem;
  color: $color-blue;
}


.property-browse-modal {
  min-width: 50% !important;
  min-height: 70vh;

  .image-gallery {
    img {
      max-width: 100% !important;
      max-height: 70vh !important;
    }

    .image-gallery-slide-wrapper {
      min-height: 55vh;
    }
  }
}

.tour360-modal {
  margin-top: 100px;
  min-width: 50% !important;
  min-height: 70vh;
}

.modal-body {
  max-width: 100% !important;
}

.property-room-container {
  max-width: 200px;

  .property-room {
    position: relative;
    padding-top: 100%;
    text-align: center;
    border-radius: 1rem;
    border: 3px solid #f1ecec;

    .property-room-details {
      position: absolute;
      width: 80%;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
    }
  }

  .image-gallery-slide {
    min-height: 60vh;

    image {
      margin: auto;
    }

    .image-gallery-slide-wrapper {
      min-height: 55vh;
    }
  }

  svg {
    font-size: 2rem;
    color: #6f6b6b;
  }

  p {
    padding: 0.5rem 0 !important;
    margin: 0 !important;
  }
}

/** Property Gallery Modal**/
.image-gallery {
  .image-gallery-svg {
    height: 5rem !important;
    width: 2.5rem !important;
  }
}

.covid-action-banner {
  text-align: center;
  background-color: #0071aa;
  color: white;

  h6 {
    margin: 0;
    padding: 0.25rem;
  }

  button {
    color: #fff;
    cursor: pointer;
    padding: 4px 16px;
    line-height: 1;

    &:hover {
      color: #fff;
    }
  }
}

.input-error {
  border: 2px solid red;
}

.check-review {
  h5 {
    font-size: 1.375rem;
    margin: 0 auto 1rem;
  }

  p.agree-terms {
    font-size: 1.25rem;
  }

  ul li {
    margin-bottom: 0.5rem !important;
  }

  .btn {
    min-width: 12rem;
  }

}

.checkout-payment {
  .btn {
    min-width: 12rem;
  }
}

.rental-application-block {
  padding-top: 150px;
  padding-bottom: 100px;

  h2 {
    margin-bottom: 50px;
    text-align: center;
    color: $color-blue;
  }

  .form-group {
    position: relative;
  }

  label{
    margin-left: 10px;
    &.required {
      color: unset;
      &:before {
        position: absolute;
        left: 0;
        top: -3px;
        content: " *";
        color: red;
        display: inline;
        font-size: 1.5rem;
        font-weight: bold;
      }
    }
  }

  .react-datepicker-wrapper {
    .form-control {
      height: 35px;
    }
  }

}




