@import url("https://fonts.googleapis.com/css?family=PT+Serif:400,700&display=swap");
@import url("https://fonts.googleapis.com/css?family=Raleway:400,500,600,700&display=swap&subset=all");


/** Layout**/
#root {
    min-height: 100vh;
}

section {
    padding: 48px 0;
}

.container {
    @media (min-width: 1400px) {
        max-width: 1300px;
    }
}

/* typography */

html {
    font-size: 16px;
}

* {
    font-family: "Raleway", sans-serif;
    // Raleway font number issue
    font-variant-numeric: lining-nums;
    -moz-font-feature-settings: "lnum" 1;
    -moz-font-feature-settings: "lnum=1";
    -o-font-feature-settings: "lnum" 1;
    -webkit-font-feature-settings: "lnum" 1;
    font-feature-settings: "lnum" 1;
}

body {
    margin: 0 auto;
    background-color: white;
}

a {
    text-decoration: none !important;

    &:hover {
        cursor: pointer !important;
        outline: 0 !important;
    }
}

button {
    &:hover, &:active, &:focus {
        outline: 0 !important;
    }
}


input:hover, input:active, input:focus {
    box-shadow: none !important;
    outline: 0 !important;
}

h1,
h2,
h3,
h4,
h5 {
    font-family: "PT Serif", sans-serif;
    font-weight: 500;
}

h1,
h2 {
    font-size: 2rem;
    margin: 0 auto 1rem;
}

h3 {
    font-size: 1.625rem;
}

h4 {
    font-size: 1.5rem;
}

h5 {
    font-size: 1.25rem;
}

h6 {
    font-size: 0.8rem;
}

p {
    color: #3a3a3a;
    font-size: 1.125rem;
    line-height: 1.8rem;
}

.text-blue {
    color: $color-blue !important;
}

.text-yellow {
    color: $color-yellow !important;
}

.text-darkgray {
    color: $color-darkgray;
}

.text-gray {
    color: $color-gray;
}

.bg-blue {
    background-color: #3B87AE !important;
}

.bg-yellow {
    background-color: $color-yellow !important;
}

.bg-lightblue {
    background-color: #F8F8F8 !important;
}

.bg-grey {
    background-color: #EEEEEE !important;
}

.custom-form-control {
    font-size: 0.9rem;
    height: 3rem;
    color: #9a9a9a;
}

/** Buttons **/

.user-btn {
    padding: 10px;
    font-weight: bold;
    text-transform: uppercase;
    font-size: 1rem;
    color: #0071aa;
    text-align: center !important;
}

.user-btn-outline {
    border: 2px solid #e8c36a !important;
    //min-width: 12rem;
    box-shadow: none !important;
}

.user-btn-outline:hover,
.user-btn-outline:focus,
.user-btn-outline:active,
.user-btn-outline.active,
.open > .dropdown-toggle.user-btn-outline {
    color: white;
    background-color: #e8c36a !important;
    transition: 0.5s;
}

.user-btn-disabled {
    border: 2px solid #e8c36a !important;
    min-width: 12rem;
}

.user-btn-disabled:hover,
.user-btn-disabled:focus,
.user-btn-disabled:active,
.open > .dropdown-toggle.user-btn-disabled {
    background-color: white !important;
    min-width: 12rem;
    color: #0071aa;
}

.btn-outline-secondary {
    border: 1px solid #e8c36a !important;
    border-radius: 500px;
    color: #707070;
    min-width: 7rem;
}

.btn-outline-secondary i {
    color: #707070;
}

.btn-outline-secondary:hover,
.btn-outline-secondary:focus,
.btn-outline-secondary:active,
.open > .dropdown-toggle.btn-outline-secondary {
    color: white;
    background-color: #e8c36a !important;
    transition: 0s;
}

.custom-btn-secondary {
    background: rgba(232, 195, 106, 0.85) !important;
    border: 0;
    border-radius: 10px;
    height: 2.6rem;
}

.custom-btn-secondary-2 {
    margin: 4.3rem 0rem 0.5rem 0.4rem;
}

.custom-btn-secondary:hover,
.custom-btn-secondary:focus,
.custom-btn-secondary.active,
.open > .dropdown-toggle.custom-btn-secondary {
    color: #0071aa;
    background-color: rgba(255, 255, 255, 0.85) !important;
    transition: 0s;
}

/** ----------- BACKGROUNDS ----------- **/

.main-background {
    background: linear-gradient(to bottom, rgba(255, 255, 255, 1), rgba(255, 255, 255, 0.3), transparent),
    url(../assets/couch-background.jpg) no-repeat center;
    background-size: cover;
    padding: 3rem 3rem 1rem 3rem;
    height: 600px;
    position: relative;
    @media screen and (max-width: 500px) {
        padding-top: 100%;
        height: unset;
    }
}

.main-background-property {
    background: linear-gradient(to top, rgba(0, 0, 0, 0.8), transparent, rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.8)),
    url(../assets/stayinboston.jpg) no-repeat center;
    background-size: cover;
    height: 400px;
}

.main-background-2 {
    background: linear-gradient(to bottom, rgba(255, 255, 255, 1), rgba(255, 255, 255, 0.9), rgba(255, 255, 255, 0.4)),
    url(../assets/boston-2.jpg) no-repeat center;
    background-size: cover;
    height: 220px;
}

.main-background-3 {
    background: linear-gradient(to bottom, rgba(255, 255, 255, 1), rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0.4)),
    url(../assets/boston-3.jpg) no-repeat center;
    background-size: cover;
    height: 455px;
}

@media (max-width: 992px) {
    .main-background {
        background: linear-gradient(to bottom, rgba(255, 255, 255, 1), rgba(255, 255, 255, 0.8), transparent),
        url(../assets/starsofboston.jpg) no-repeat center;
        background-size: cover;
    }

    .main-background-2 {
        background: linear-gradient(
                to bottom,
                rgba(255, 255, 255, 1),
                rgba(255, 255, 255, 0.8),
                rgba(255, 255, 255, 0.4)
        ),
        url(../assets/boston-2.jpg) no-repeat center;
        background-size: cover;
    }
}
