/**
** Property List
 */
.property-listing-block {
  padding: 40px 15px;

  .property-filter {
    padding: 0;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    text-decoration: none;
    text-transform: capitalize;
    color: $color-blue !important;
    font-size: 1rem;
    background: transparent;
    border: 1px solid $color-yellow !important;
    border-bottom: none !important;
    border-radius: 5px 5px 0 0 !important;

    .col-md {
      padding: 1rem;
    }

    &:hover,
    &:active {
      text-decoration: none !important;
      color: #0071aa;
      font-weight: bold;
    }

    &::before {
      background: none repeat scroll 0 0 #e8c36a;
      bottom: 0;
      content: "";
      display: block;
      height: 2px;
      left: -2px;
      position: absolute;
      transition: width 0.3s ease 0s, left 0.3s ease 0s;
      width: 100%;
    }

    &::after {
      background: none repeat scroll 0 0 transparent;
      bottom: 0;
      content: "";
      display: block;
      height: 3px;
      left: 50%;
      position: absolute;
      transition: width 0.3s ease 0s, left 0.3s ease 0s;
      width: 0;
    }

    &:hover::after {
      width: 100%;
      left: 0;
    }

    &:active::after {
      background: none repeat scroll 0 0 #0071aa;
      bottom: 0;
      content: "";
      display: block;
      height: 2px;
      left: 0;
      position: absolute;
      width: 100%;
    }
  }

  .property-category-border {
    width: 100%;
    border-top: 3px solid $color-blue;
    margin: 2rem 0;
  }

  .tab-content {
    width: 100%;
  }

}

.property-search-box {
  margin: -210px auto 30px;
  padding: 24px 32px;
  border-radius: 8px;
  background: rgba(255, 255, 255, 0.85);
  box-shadow: 0 5px 25px 0 rgba(0, 0, 0, 0.16);

  p {
    display: block;
    margin: 10px auto 0;
    font-size: 16px;
    line-height: 1.2;

    svg {
      width: 8px!important;
      fill: red;
      color: red;
    }
  }

  @media screen and (min-width: 600px) and (max-width: 767px) {
    .btn-wrapper {
      flex-direction: row !important;
      justify-content: space-around;

      .btn {
        max-width: 220px !important;
      }
    }
  }
  @media (max-width: 992px) {
    .btn {
      font-size: .9rem !important;
    }
  }
}


/**
** Property detail
 */

.property-detail-banner-block {
  position: relative;
  margin: 125px 0 0;
  padding-bottom: 0;
  background-color: #f2f2f3;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  height: 450px;

  .property-banner-buttons-wrapper {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    height: 100%;

    .btn {
      padding: 8px 10px;
      display: flex;
      justify-content: center;
      align-items: center;
      color: white;
      height: 3rem;
      background-color: #e8c36a;
      border: 1px solid #e8c36a !important;
      text-transform: uppercase;
      border-radius: 10px;
      min-width: 6rem;

      i {
        margin: 0;
      }
    }
  }

  @media screen and (max-width: 768px) {
    height: 60vw;
  }
}

.property-detail-content-block {
  .property-feature-icons-wrapper {
    font-size: 0.8rem;

    svg {
      font-size: 1rem;
      color: #cbcbcb;
      margin-right: 2px;
    }

    span {
      margin: 0 1rem 1rem 0;

      i {
        margin: 0 0.5rem 0 0;

      }
    }
  }

  .property-detail-tab-buttons {
    margin: 1.5rem 0;
    display: flex;
    justify-content: space-around;
    border: none;

    .nav-item {
      margin: .5rem 0;

      .btn {
        height: 3rem;
        border: 1px solid #e8c36a !important;
        border-radius: 15px;
        color: #0071aa;
        min-width: 8rem;
        font-weight: normal;
      }
    }

    @media (max-width: 992px) {

    }
  }
}


