@import url('https://brick.a.ssl.fastly.net/Raleway:400');
// Here you can add other styles

.c-app {
  @include font-size(16);

  a {
    @include font-size(16);
  }
}

.table svg {
  width: .8rem !important;
  height: .8rem;
  margin-right: .5rem !important;
}

main.c-main {
  min-height: 75vh !important;

  .c-app {
    min-height: 75vh;
  }
}


.property-row {
  display: flex;

  div.card-body {
    min-height: 60vh !important;
  }

  .property-action-card {
    align-self: start;

    .btn {
      margin: 1rem 0;
    }

    .property-gallery {
      width: 50%;
      margin: 1rem auto;

      .image-gallery-icon {
        svg {
          width: 20px;
          height: 20px;
        }

        svg * {
          width: 100%;
          height: 100%;
          fill: $color-lightblue;
        }
      }
    }
  }
}

#bedTypeSelection {
  .card-body {
    min-height: 30vh !important;
  }
}

.edit-btn {
  color: $blue;
}


div.quill {
  min-height: 35vh !important;

  .ql-container {
    min-height: 30vh !important;
  }
}

.content-title .quill {
  min-height: 50px !important;
}

.location-marker {
  i {
    font-size: 2.5rem;
    color: red;
    width: 3rem !important;
    height: 3rem !important;
  }

  svg {
    color: red;
    width: 3rem !important;
    height: 3rem !important;
  }

}

.autocomplete-container {
  width: 100%;
  position: relative;

  .place-input-wrapper {
    position: relative;
    input {
      width: 100%;
      font-size: 1rem;
    }

    svg {
      position: absolute;
      right: 1rem;
      top: .7rem;
    }
  }

  .autocomplete-dropdown-container {
    position: absolute;
    top: 40px;
    border-left: 1px solid lightgray;
    border-right: 1px solid lightgray;
    font-size: 1rem;
    z-index: 9999;
    width: 100%;

    .suggestion-item {
      border-bottom: 1px solid lightgray;
      padding: .5rem;
      cursor: pointer;
      background: #fff;

      svg {
        margin: 0 10px;
      }

      &.active {
        background-color: lightgray;
      }
    }
  }
}

.property-action-card {
  .featured-img-wrapper {
    width: 50%;
    margin: 1rem auto;
    overflow: hidden;
    height: auto;

    .featured-img {
      max-width: 100%;
      height: auto;
      object-fit: cover;
      object-position: 50% 50%;
      border-radius: 0 !important;
    }
  }
}

.media-modal {
  max-width: 90%;

  .row {
    justify-content: start;
  }

  .tab-pane {
    padding: 2rem;
  }

  .modal-title {
    width: 100%;
    padding: 0 2rem;

    h5 {
      width: 100%;
    }
  }

  .media-toolbar {
    margin: 2rem 0;
  }

  .modal-content {
    min-height: 800px;
    height: auto;

    .modal-body {
      height: auto;

      .tab-content {
        height: auto;
      }
    }
  }

  .gallery-item {
    width: 10%;
    padding: 0 !important;
    margin: 1rem;
    box-shadow: 0 6px 12px 0 rgba(0, 0, 0, 0.15);

    .media-thumbnail-wrapper {
      width: 100%;
      border: 3px solid $color-lightgray;
      height: 100px;
      overflow: hidden;

      .media-thumbnail {
        //max-width: 100%;
        object-fit: cover;
        object-position: 50% 50%;
        border-radius: 0 !important;
      }

      svg {
        position: absolute;
        top: 0;
        right: 0;
        fill: $color-lightblue;
      }
    }

    p {
      font-size: .75rem;
      text-align: center;
      padding: .5rem !important;
      margin: 0 !important;
    }
  }

  .item-active {
    border: 3px solid $color-lightblue;
    //box-shadow: inset 0 0 0 1px rgba(0, 0, 0, .1);
    box-shadow: 0 10px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    -moz-box-shadow: 0 10px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    -webkit-box-shadow: 0 10px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  }

  .media-modal-action {
    float: right !important;
  }
}

.media-sidebar-thumbnail-wrapper {
  width: 100%;
  border: 3px solid $color-lightgray;
  min-height: 100px;
  background-color: $color-lightgray;

  .media-sidebar-thumbnail {
    max-width: 100%;
    height: auto;
    margin: auto;
    object-fit: cover;
    object-position: 50% 50%;
    border-radius: 0 !important;
  }
}

.amenity-selection {
  display: flex;
  flex-wrap: wrap;

  .switch-wrapper {
    flex: 0 0 50%;
    display: flex;
    align-items: center;
    padding: 1rem 2rem;
  }
}

.media-scroll-gallery {
  overflow: scroll !important;
  height: 60vh;
  border: 2px solid #eeeeee;
  align-items: start !important;
}

.table-img-thumbnail {
  width: 100px;
  object-fit: cover;

  img {
    height: 100px;
  }
}

.required {
  color: #f00;
}

.SortableHelper {
  z-index: 9999;
}

.infinite-scroll-wrapper {
  height: 70vh;
  overflow: auto;
  min-height: 0 !important;
}

.pagination-wrapper {
  display: flex;
  justify-content: center;

}

.page-size-selection {
  max-width: 150px;
}

.ReactCrop {
  width: 100%;

  div {
    width: 100%;
  }

  .ReactCrop__image {
    width: 100%;
  }
}

.crop-modal {
  padding: 2rem;
}

.crop-modal-action {
  float: right !important;
}

.rbc-date-cell {
  h4 {
    font-size: 24px;
  }

  span {

  }
}
