//////////////////////////////////
//          Layout              //
//////////////////////////////////

.navbar {
  margin: 0 auto;
  background-color: transparent !important;
  padding-top: 0 !important;
  padding-bottom: 0 !important;
  display: flex;
  justify-content: space-between;

  &.fixed-top {
    z-index: 1000;
    position: fixed;
    top: 50px;
    left: 0;
    right: 0;
    opacity: 1;
    transition: all ease-in-out 500ms;
  }

  &.sticky {
    top: 0 !important;
    background-color: #fff !important;
    box-shadow: 0 2px 16px 0 rgba(0, 0, 0, 0.14);
    padding: 15px 0;
  }

  .navbar-brand {
    img {
      margin-right: auto;
      max-width: 250px !important;
      min-width: 150px !important;
    }
  }

  .navbar-collapse {
    height: auto !important;
    flex-grow: 0;

    .navbar-nav {
      display: flex;
      flex-direction: column;
      justify-content: center;
      margin-bottom: 30px;

      .nav-item {
        margin: 0;

        .nav-link {
          margin: 0;
          padding: 8px;
          font-weight: bold;
          line-height: normal;
          color: $color-blue;
          position: relative;
          text-transform: uppercase;
          text-decoration: none;

          &:after {
            content: "";
            position: absolute;
            width: 100%;
            height: 3px;
            bottom: -3px;
            left: 0;
            background-color: $color-blue;
            visibility: hidden;
            transform: scaleX(0);
            transition: all 0.3s ease-in-out 0s;
          }

          &:hover,
          &.active {
            &:after {
              visibility: visible;
              transform: scaleX(1);
            }
          }
        }
      }
    }
  }

  .navbar-toggler {
    z-index: 1000;
    position: absolute;
    top: 10px;
    right: 10px;
    display: block;
    height: 30px;
    width: 30px;
    cursor: pointer;
    padding: 0;
    border: 0;
    background: transparent;
    outline: 0;
    box-shadow: none;

    > span {
      position: absolute;
      top: 0;
      display: block;
      width: 100%;
      height: 4px;
      background: $color-blue none repeat scroll 0 0;
      opacity: 1;
      transition: all 600ms cubic-bezier(0.62, 0.04, 0.3, 1.56);
      transition-delay: 100ms;
      transform: rotate(0deg);

      &:nth-child(1) {
        transform: rotate(135deg);
        margin-top: 10px;
      }

      &:nth-child(2) {
        opacity: 0;
        // left: -30px * 3;
      }

      &:nth-child(3) {
        transform: rotate(-135deg);
        margin-top: 10px;
      }
    }

    &.collapsed {
      span {
        position: absolute;
        top: 0;
        display: block;
        width: 100%;
        height: 4px;
        background: $color-blue none repeat scroll 0% 0%;
        opacity: 1;
        transition: all 600ms cubic-bezier(0.62, 0.04, 0.3, 1.56);
        transition-delay: 100ms;
        transform: rotate(0deg);

        &:nth-child(1) {
          margin-top: 0px;
          color: $color-blue;
        }

        &:nth-child(2) {
          margin-top: 10px;
          margin-right: 0px;
          color: $color-blue;
        }

        &:nth-child(3) {
          margin-top: 20px;
          color: $color-blue;
        }
      }
    }
  }

  @media (max-width: 768px) {
    .navbar-brand {
      img {
        width: 200px;
      }
    }
  }

  @media (max-width: 992px) {
    max-height: 9rem;
    .navbar-toggler {
      display: flex;
    }
    .navbar-collapse {
      background: $color-black;
      padding: 83px 0 20px;
      position: fixed;
      top: 0;
      right: 0;
      height: auto;
      max-width: 500px;
      width: 0;
      opacity: 0;
      transition: all 600ms cubic-bezier(0.62, 0.04, 0.3, 1.56);
      transition-delay: 100ms;

      .navbar-nav {
        .nav-item {
          .nav-link {
            padding: 20px;
            text-align: center;

            &:after {
              content: "";
              width: 0;
            }

            &:hover {
              background-color: $color-lightblue;

              &:after {
                content: "";
                width: 0;
              }
            }
          }
        }
      }

      &.show {
        max-width: 500px;
        width: 100%;
        opacity: 1;
      }
    }
  }

  @media (min-width: 992px) {
    .navbar-toggler {
      display: none;
    }
    .navbar-collapse {
      .navbar-nav {
        flex-direction: row;

        .nav-item {
          padding: 0;
        }
      }
    }
  }

  @media (min-width: 1176px) {
    .navbar-collapse {
      .navbar-nav {
        .nav-item {
          padding: 0 8px;
        }
      }
    }
  }
  
  @media (min-width: 1176px) {
    .navbar-collapse {
      .navbar-nav {
        .nav-item {
          padding: 0 12px;
        }
      }
    }
  }
}

.footer {
  padding: 105px 24px 93px;
  font-size: 1rem;
  background-color: #EEEEEE;

  .footer-logo {
    max-width: 300px;
    width: 100%;
  }

  h5 {
    color: $color-blue;
  }

  .social-icons-group {
    svg {
      color: #3a3a3a;
      fill: #3a3a3a;
      width: 20px;
      height: 20px;
      margin: 1rem 1.5rem 1rem 0;

      &:hover {
        color: #949494;
      }
    }
  }

  a {
    color: #3a3a3a;
    word-wrap: break-word;
  }

  @media (max-width: 992px) {
    padding: 2rem;
  }
}

.alert-container {
  max-width: 80%;
  position: fixed;
  right: 3rem;
  top: 3rem;
  z-index: 9999;

  svg {
    margin-right: 8px;
    width: 20px;
    height: 20px;
  }
}

.admin-navbar {
  text-align: right;
  background-color: #303c54;
  padding: 0 50px;
}

