.alert-danger {
  background-color: #ff9800;
  min-width: 300px;
  text-align: center;
  color: white;
}

.react-datepicker-wrapper {
  margin: 0 auto;
  display: block;
  width: 100%;

  .input-group {
    display: flex;
    align-content: center;
  }

  .form-control {
    border: 1px solid #ced4da !important;
    font-size: 0.9rem;
    height: 3rem;
  }
}

.solution-card {
  margin: 0 auto;
  max-width: 370px;
  min-height: 550px;
  height: 100%;
  padding: 0;
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  box-shadow: 0 6px 12px 0 rgba(0, 0, 0, 0.15);

  img {
    max-width: 100%;
    border-radius: 8px 8px 0 0;
    margin: 0;
    padding: 0;
  }

  .background-loader {
    background-color: #ebedef;
    border-radius: 8px 8px 0 0;
    width: 100%;
    height: 250px;
  }

  .solution-content {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    padding: 38px 28px 28px;

    h4 {
      margin-top: 0;
      color: $color-blue;
    }

    p {
      font-size: 1rem;
    }

    a {
      margin: auto 0 0 auto;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 16px;
      min-width: 8rem;
      border-radius: 10px;
    }
  }
}

.property-card {
  position: relative;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  margin: 0 auto;
  padding: 0;
  max-width: 400px;
  min-height: 450px;
  width: 100%;
  border-radius: 8px;
  box-shadow: 0 6px 12px 0 rgba(0, 0, 0, 0.15);
  background-color: #fff;

  .property-card-image-wrapper {
    position: relative;
    margin: 0;
    width: 100%;
    padding-top: 66.67%;
    background-color: #ebedef;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border-radius: 8px 8px 0 0;

    .card-title-wrapper {
      position: absolute;
      transform: translateX(-50%);
      left: 50%;
      bottom: 10px;
      width: 90%;
      padding: 12px;
      text-align: center;
      display: flex;
      background: rgba(0, 0, 0, 0.25);
      //filter: blur(1px);

      h4 {
        font-weight: bold;
        margin: 0 auto;
        color: white !important;
      }
    }
  }

  .property-card-content {
    display: flex;
    flex-direction: column;
    padding: 24px 24px 32px;
    flex-grow: 1;

    .features-wrapper {
      ul {
        display: flex;
        flex-wrap: wrap;
        margin: 0;
        padding: 0;

        li {
          list-style: none;
          font-size: 0.9rem;
          margin: 0.75rem 0;

          svg {
            font-size: 1rem;
            color: #cbcbcb;
            margin-right: 2px;
          }
        }

        li:nth-child(even) {
          flex: 0 0 50%;
        }

        li:nth-child(odd) {
          flex: 0 0 50%;
        }
      }
    }

    .price-wrapper {
      margin-top: auto;
      padding: 8px 0 8px;
      text-align: center;

      h4 {
        margin: 8px auto;
        padding: 0;
        color: #e8c36a;
        font-family: Raleway, sans-serif;
        font-weight: normal;
        font-size: 1.125rem;

        span {
          color: #3c4b64;
          margin: 0;
        }
      }
    }

    .property-buttons-wrapper {
      display: flex;
      justify-content: space-around;
      align-items: center;

      .btn {
        min-width: 8rem;
        font-size: .875rem;
      }
    }
  }

  &:hover .overlay {
    opacity: 0.8;
    cursor: pointer;
  }

  @media (max-width: 400px) {
    .property-card-image-wrapper {
      padding-top: 200px;

      .card-title-wrapper {
        h4 {
          font-size: 1.25rem;
        }
      }
    }
    .property-card-content {
      .features-wrapper {
        ul {
          li {
            flex: 0 0 100% !important;
          }
        }
      }

      .property-buttons-wrapper {
        .btn {
          min-width: auto !important;
          padding: 4px;
        }

        //flex-direction: column;
        //
        //.btn:nth-child(1) {
        //    margin-bottom: 20px;
        //    min-width: 8rem;
        //}
      }
    }
  }
}

.overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 50%;
  width: 100%;
  opacity: 0;
  transition: 0.2s ease;
  background: linear-gradient(to bottom, #0071aa, #0071aa, transparent), no-repeat center;
  border-radius: 8px 8px 0 0;
  cursor: pointer;
}

#covid-banner-modal {
  .modal-title {
    color: $color-blue !important;
  }
}

.modal-content {
  .close {
    span {
      color: $color-darkgray;
    }
  }
}

.underlined-link {
  font-size: 1.125rem;
  font-weight: bold;
  text-decoration: underline;
  color: #3a3a3a !important;
}

.edit-text-block {
  h2, h4, h5 {
    margin-top: 0;
    margin-bottom: 1rem;
  }
}

.text-loader {
  margin-bottom: 24px;
  margin-right: 24px;
  background-color: #ebedef;
  width: 100%;
  height: 50px;
  border-radius: 8px;
}

.booking-information {
  .react-datepicker-popper {
    .react-datepicker {
      .react-datepicker__navigation {
        top: 20px;
      }

      .react-datepicker__month-container {
        .react-datepicker__header {
          .react-datepicker__current-month {
            font-size: 24px;
          }

          .react-datepicker__day-name {
            width: 50px;
            height: 40px;
          }
        }

        .react-datepicker__month {
          .react-datepicker__week {
            display: flex;

            .react-datepicker__day {
              width: 50px;
              height: 40px;

              h6 {
                margin: auto;
                font-size: 16px;
                font-weight: bold;
              }

              .past-date {
                color: $color-gray;
                width: 50px;
                height: 40px;
                position: relative;
                display: flex;
                align-items: center;
                text-decoration: line-through !important;
                text-decoration-color: gray !important;
              }

              .booked-date {
                width: 50px;
                height: 40px;
                position: relative;
                display: flex;
                color: $color-gray;
                align-items: center;
                text-decoration: line-through !important;
                text-decoration-color: $color-gray !important;
              }

              .date-detail {
                h6 {
                  margin: 4px auto;
                }

                span {
                  margin: 0 auto;
                  padding: 0;
                  display: block;
                  font-size: 12px;
                  line-height: 1.1;
                }
              }
            }
          }
        }

      }
    }

    @media (max-width: 768px) {
      max-width: 90vw !important;
      left: -2rem !important;
      .react-datepicker__triangle {
        left: 100px;
      }
      .react-datepicker {
        max-width: 90vw !important;

        .react-datepicker__month-container {
          max-width: 90vw !important;

          .react-datepicker__header {
            max-width: 90vw !important;

            .react-datepicker__current-month {
              font-size: 24px;
            }

            .react-datepicker__day-name {
              width: 11vw !important;
            }
          }

          .react-datepicker__month {
            max-width: 90vw !important;

            .react-datepicker__week {
              .react-datepicker__day {
                width: 11vw !important;

                .past-date, .booked-date, .date-detail {
                  width: 11vw !important;
                }
              }
            }

          }
        }
      }
    }
  }

}

// react-big-calendar
.calendar-container {
  height: 500px;

  .rbc-row-content {
    height: 100%;

    .rbc-row {
      height: 100%;

      .rbc-date-cell {
        height: 100%;

        .calendar-date-cell {
          height: 100%;
          display: flex;
          text-align: center;
          align-content: space-around;
          flex-flow: row wrap;

          h4 {
            width: 100%;
            margin: 0;
            font-weight: bold;
          }

          h6 {
            margin: 0;
            width: 100%;
          }
        }
      }
    }
  }

}

.CookieConsent {
  & > div:nth-child(1) {
    p {
      margin: 0 auto;
      text-align: center;
      font-size: 16px;
      color: #fff;
    }
  }

  .accept-button {
    color: #4e503b;
    font-size: 16px;
  }

  .decline-button {
    color: #4e503b;
    font-size: 16px;
  }
}

.email-confirm-modal {
  margin-top: 2rem;
  max-width: 450px;
  font-size: 1rem;
  .modal-body {
    svg {
      margin-right: 10px;
      width: 30px;
      height: 30px;
      color: #0071aa;
    }
  }
}
